export const savedJobsReducer = (state = [], action) => {
  switch (action.type) {
    case 'SAVE_JOB':
      const newJob = state.find(job => console.log(job.id));
      console.log(action.job.id);
      console.log(newJob);
      return [...state, action.job];
    default:
      return state;
  }
};